social .social-icon .github{background-image:url("/dist/images/github.svg");background-repeat:no-repeat;background-position:center;background-size:contain;padding:45px 45px 0 0;width:100%;transition:all .2s ease-in-out}section#social .social-icon .github:hover{filter:brightness(400%);transition:all .2s ease-in-out}section#social .social-icon:hover{background-size:100% 100%}section#social .social-icon:last-child{padding-bottom:0}footer{width:100%}footer .content{padding-bottom:25px;display:flex}footer .content p{font-family:'Poppins', sans-serif;font-weight:500;font-size:1.3rem;color:#5E5E5E;text-transform:none}.bg{background-image:url("/dist/images/bg.png");background-repeat:no-repeat;position:fixed;background-size:100% 100%;bottom:0;left:0;opacity:0.5;height:30%;width:100vw;z-index:-1}.bg-right{position:absolute;height:auto;width:50rem;bottom:0;right:95px;z-index:-1}@media (max-width: 768px){section#social{order:-1;position:relative;flex-direction:row;top:auto;left:auto;width:100%;height:45px;align-items:center;justify-content:center;margin:35px 0}section#social .social-icon{margin:0 15px}section#social .social-icon .facebook{padding:8px 8px}section#social .social-icon .instagram{padding:12px 12px}section#social .social-icon .github{padding:12px 12px}section#login .content{max-width:auto;width:100%}section#login .content img{width:auto}.bg-right{left:0;height:25rem;bottom:70px}footer .content{justify-content:center;align-items:center;text-align:center}footer .content p{background-color:#EEE1;backdrop-filter:blur(12px)}}
